import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import forceRenderPercentForAutoFitAsl from './fixtures/tableForceRenderPercentForAutoFit.json';

describe('Zhi:table', () => {
  it('forceRenderPercentForAutoFit', () => {
    const asl = ['root', forceRenderPercentForAutoFitAsl];

    const { container } = render(<Zhi content={asl} config={{
      table: {
        forceRenderPercentColWidthForAutoFit: () => true,
      }
    }} />);
    const table = container.querySelector('table');
    expect(table?.style.width).toBe('100%');
    const cols = table?.querySelectorAll('col')!;
    expect(cols[0].width).toBe('25%');
    expect(cols[1].width).toBe('75%');
  });
});
